<template>
  <v-col xs="12" sm="6" md="4" lg="3" xl="3">
    <v-hover>
      <template v-slot:default="{ hover }">
        <v-card class="mx-auto" max-width="244">
          <v-row v-show="hover">
            <v-col>
              <edit-camping
                v-on:getVal="getVal"
                :dialogController="dialogEdit"
                :campingData="camping"
                :allProjectData="allProject"
              />
              <delete-camping v-on:getVal="getVal" :campingData="campingData" />
            </v-col>
          </v-row>
          <v-img
            :lazy-src="mainUrl + campingData.image_url"
            :src="mainUrl + campingData.image_url"
            height="200px"
          ></v-img>
          <v-hover open-delay="1000" v-slot="{ hover }">
            <v-row>
              <v-col>
                <v-chip
                  id="chip"
                  outlined
                  v-if="campingData.vedio_url"
                  v-text="'click to show video'"
                  :class="hover ? 'secondary' : 'primary'"
                >
                </v-chip>
                <v-card-text
                  id="video"
                  v-if="hover"
                  v-html="campingData.vedio_url"
                ></v-card-text>
              </v-col>
            </v-row>
          </v-hover>
          <v-card-title v-text="campingData.title[lang]"></v-card-title>
          <v-card-subtitle>
               <br>
                <p>Total Amount : &ensp; {{ campingData.total_amount }} $</p>
              
                 <p>current Amount : &ensp; {{ campingData.current_amount}} $</p>
               
            
          </v-card-subtitle>
          <v-card-subtitle>
            <v-row id="row">
              <v-col cols="12">
                <v-chip
                  outlined
                  id="nation"
                  v-if="campingData.nation"
                  v-text="campingData.nation.name[lang]"
                  class="primary"
                >
                  Default
                </v-chip>
              </v-col>

              <v-col cols="6">
                <v-chip
                  v-if="campingData.project"
                  v-text="campingData.project.title[lang]"
                  class="primary"
                >
                  Default
                </v-chip>
              </v-col>
              <v-col cols="6">
                <v-chip
                  v-if="campingData.section"
                  v-text="campingData.section.name[lang]"
                  class="secondary"
                >
                  Default
                </v-chip>
              </v-col>
            </v-row>

            <v-divider></v-divider>
          </v-card-subtitle>

          <v-card-actions>
            <v-btn @click="show = !show" color="primary" text>
              Description
            </v-btn>

            <v-spacer></v-spacer>

            <v-btn icon @click="show = !show">
              <v-icon>{{
                show ? "mdi-chevron-up" : "mdi-chevron-down"
              }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="show">
              <v-divider></v-divider>

              <v-card-text>
                {{ campingData.description[lang] }}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </template>
    </v-hover></v-col
  >
</template>
<script>
export default {
  props: ["campingData", "allProjectData"],
  components: {
    "edit-camping": () => import("./edit.vue"),
    "delete-camping": () => import("./delete.vue"),
  },
    computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  methods: {
    getVal() {
      this.$emit("getVal");
    },
  },
  data() {
    return {
      dialogEdit: false,
      mainUrl: this.$store.state.imageUlr + "camping/",
      camping: this.campingData,
      show: false,
      allProject: this.allProjectData,
    };
  },
};
</script>

<style>
#row {
  margin-bottom: 10px;
}
#child {
  /* position: absolute;
  z-index: 1023;
  right: 0px;
  bottom: -60px; */
}
#video {
  position: absolute;
  top: 0;
  z-index: 1023;
}
#chip {
  margin-top: 10px;
}
</style> 